.navigation > ul > li > a {
  display: block;
  margin: 12px 30px 12px 0;
  text-transform: uppercase;
}

.navigation > ul > li > a:hover, .navigation > ul > li > a.active {
  color: #fff;
  text-decoration: none;
}

table {
  border-collapse: collapse;
  table-layout: fixed;
  width: 100%;
}

th {
  text-align: left;
}

tr,
td,
th {
  vertical-align: middle;
}

h1 {
  font-size: 40px;
}

@media all and (min-width: 500px) {
  h1 {
    font-size: 50px;
  }
}

header {
  background: url(../../images/pattern-bg-1.svg) repeat-x top left, url(../../images/pattern-bg-2.svg) repeat-x bottom left;
  background-color: #4AD4A1;
  height: 380px;
  position: relative;
}

.header__content {
  color: #fff;
  font-size: 18px;
  left: 50%;
  margin: 0 auto;
  position: absolute;
  top: 55%;
  transform: translate(-50%, -50%);
  width: 100%;
}

.text--light {
  color: rgba(255, 255, 255, 0.7);
}

@media all and (min-width: 900px) {
  .container {
    width: 832px;
  }
}
